//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }


$ralewayFamily: 'Raleway';
$montserratFamily: 'Montserrat';
$myriadPro: 'MyriadPro';

// MyriadPro-Regular
@font-face {
	@include fontdefault(normal, 100, $myriadPro);
	@include fontpath('MyriadPro-Regular', 'MyriadPro-Regular', MyriadPro-Regular); }
// Montserrat-Light / Montserrat-UltraLight
@font-face {
	@include fontdefault(normal, 100, $montserratFamily);
	@include fontpath('Montserrat-UltraLight', 'Montserrat-UltraLight', Montserrat-UltraLight); }
@font-face {
	@include fontdefault(normal, 300, $montserratFamily);
	@include fontpath('Montserrat Light', 'montserrat-light', montserrat-light); }

// Raleway
@font-face {
	@include fontdefault(normal, 400, $ralewayFamily);
	@include fontpath('Raleway-Regular', 'Raleway-Regular', Raleway-Regular); }
@font-face {
	@include fontdefault(normal, 500, $ralewayFamily);
	@include fontpath('Raleway-Medium', 'Raleway-Medium', Raleway-Medium); }
@font-face {
	@include fontdefault(normal, 600, $ralewayFamily);
	@include fontpath('Raleway-SemiBold', 'Raleway-SemiBold', Raleway-SemiBold); }
@font-face {
	@include fontdefault(normal, 700, $ralewayFamily);
	@include fontpath('Raleway-Bold', 'Raleway-Bold', Raleway-Bold); }
@font-face {
	@include fontdefault(normal, 800, $ralewayFamily);
	@include fontpath('Raleway-ExtraBold', 'Raleway-ExtraBold', Raleway-ExtraBold); }
